@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@400;500;600;700&display=swap);
.aboutinf {
  position: absolute;
  width: 100%;
  height: 100%;
  font-size: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 600;
}

* {
  box-sizing: border-box;
}

body {
  background-color: gray;
  margin: 0px;
  height: 100%;
  width: 100%;
  font-family: "Oswald", sans-serif;
}

.movies .movie__loading {
  position: absolute;
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  font-weight: 800;
  font-size: 50px;
}

.movies__inf {
  padding: 50px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.movies__inf .movie {
  margin-top: 30px;
  padding: 20px;
  width: 700px;
  height: 330px;
  margin-right: 70px;
  margin-bottom: 70px;
  background-color: white;
  display: flex;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

.movies__inf .movie:hover {
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

.movie .movie__image {
  max-width: 200px;
  max-height: 250px;
  position: relative;
  top: -30px;
  padding-right: 50px;
}

.movie__inf h1 {
  color: black;
  font-weight: 800;
  font-size: 30px;
}

.movie__inf h4,
h5,
ul {
  color: black;
  opacity: 0.6;
  font-weight: 500;
}

.movie__inf .movie__genres {
  display: flex;
  list-style: none;
  padding: 0px;
}

.movie__genres .movie__genre {
  margin-right: 15px;
}

.movie__detailinf {
}

.link {
  position: absolute;
  margin-left: 1%;
  margin-top: 2%;
  width: 3%;
  display: flex;
  flex-direction: column;
  padding: 5px;
  border: 2px solid black;
  background-color: aliceblue;
  align-items: center;
  font-size: 20px;
  z-index: 1;
}

.homelink {
  margin-bottom: 5px;
}

.movie__detailinf {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  font-size: 25px;
}

.movie__detail__title {
  font-size: 40px;
}

.movie__detail__genres {
  margin-left: 10px;
}
.movie__detail__summary {
  width: 50%;
}

